(() => {

  const filterNav = document.querySelector('.category-selector');
  const hiddenElements = document.querySelectorAll('.searchform .hidden');
  let tabValue,
      tabContent;

  const SelectCategory= () => {

    if(filterNav) {

      hiddenElements.forEach((element) => {
        element.classList.add('hidden');
      });

      function selectTabValue() {
        tabValue = filterNav.options[filterNav.selectedIndex].value;
        tabValue = +tabValue;
        selectTabContent(tabValue);
      }

      selectTabValue();

      function selectTabContent(tab) {
        customForEach(hiddenElements, function (index, item) {
          tabContent = item.getAttribute('data-tab');
          tabContent = JSON.parse(tabContent);
          for (let tabNums in tabContent) {
            let tabNum = tabContent[tabNums];
            if (tabNum === tab) {
              item.classList.remove('hidden');
            }
          }
        });
      }
    }
  }

  if(filterNav) {
    window.onload = SelectCategory();
    filterNav.addEventListener('change', SelectCategory);
  }
  
})();