(() => {

  const sliderAuto = (slider, miliseconds) => {
    slider.isLastSlide = () => {
      return slider.page >= slider.dots.childElementCount - 1;
    }

    let slide = () => {
      slider.slideTimeout = setTimeout(() => {
        const slideTo = () => {
          return slider.isLastSlide() ? 0 : slider.page + 1;
        }

        slider.scrollItem(slideTo(), true);
      }, miliseconds);
    }

    slider.ele.addEventListener('glider-animated', (event) => {
      window.clearInterval(slider.slideTimeout);
      slide();
    });

    slide();
  }

  const teamSliderVar = document.querySelector('.team-slider');
  const expertsSliderVar = document.querySelector('.experts-slider');
  const partnersSliderVar = document.querySelector('.partners-slider');

  if(teamSliderVar) {
    const teamSlider = new Glider(teamSliderVar, {
      slidesToShow: 1,
      dots: '.dots',
      draggable: true,
      dragVelocity: 4,
      responsive: [
        {
          breakpoint: 425,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            draggable: true,
          }
        }, {
          breakpoint: 650,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 1,
            draggable: true,
          }
        }, {
          breakpoint: 992,
          settings: {
            slidesToShow: 4,
          }
        }
      ]
    })
    sliderAuto(teamSlider, 4000);
  }

  if(partnersSliderVar) {
    const partnersSlider = new Glider(partnersSliderVar, {
      slidesToShow: 1,
      slidesToScroll: 1,
      dots: '.dots',
      draggable: true,
      responsive: [
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 3,
          }
        },{
          breakpoint: 1024,
          settings: {
            slidesToShow: 5,
            slidesToScroll: 3,
          }
        }
      ]
    })
    sliderAuto(partnersSlider, 4000);
  }

  if(expertsSliderVar) {
    const expertsSlider = new Glider(expertsSliderVar, {
      slidesToShow: 1,
      slidesToScroll: 1,
      dots: '.dots',
      draggable: true,
      dragVelocity: 4,
      responsive: [
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 2,
          }
        },{
          breakpoint: 1024,
          settings: {
            slidesToShow: 4,
            slidesToScroll: 3,
          }
        }
      ]
    })
  }

})();