function customForEach(array, callback, scope) {
  for (let i = 0; i < array.length; i++) {
    callback.call(scope, i, array[i]);
  }
};

Array.prototype.remove = function(value) {
  var idx = this.indexOf(value);
  if (idx != -1) {
    return this.splice(idx, 1);
  }
  return false;
}

const createCookie = (name, value, days) => {
  var expires = '',
    date = new Date();
  if (days) {
    date.setTime(date.getTime() + (days * 24 * 60 * 60 * 1000));
    expires = '; expires=' + date.toGMTString();
  }
  document.cookie = name + '=' + value + expires + '; path=/';
}

const getCookie = (name) => {
  var value = "; " + document.cookie;
  var parts = value.split("; " + name + "=");
  if (parts.length == 2) return parts.pop().split(";").shift();
}

const setChoosensCookie = (arr) => {
  let stringifyArr = JSON.stringify(arr);
  createCookie('choosensArr', stringifyArr, '30');
}

const deleteCookie = (name) => {
  document.cookie = name + '=;expires=Thu, 01 Jan 1970 00:00:01 GMT;';
};

const uniqueArr = (arr) => {
  let result = [];
  for (let str of arr) {
    if (!result.includes(str)) {
      result.push(str);
    } else {
      result.remove(str);
    }
  }
  return result;
}

(() => {

  const findChoosenId = (choosen) => {
    let choosenId = choosen.id;
    return choosenId;
  }

  const elementToArr = (arr, element) => {
    if(element != '') {
      arr.push(element);
    }
    return uniqueArr(arr);
  }

  const choosens = document.querySelectorAll('.choosensJs');
  let favoritesFromCookies;

  if(getCookie('choosensArr') && getCookie('choosensArr') != 'undefined') {
    favoritesFromCookies = JSON.parse(getCookie('choosensArr'));
  }
  
  let choosensArr = [];

  if(favoritesFromCookies) {
    choosensArr = favoritesFromCookies;
  }

  if(choosens) {
    customForEach(choosens, function (index, choosen) {
      const choosenBtn = choosen.querySelector('.ChoosenBtnJs');
      choosenBtn.addEventListener('click', () => {
        console.log('favorites to cookie');
        choosenBtn.classList.toggle('active-svg');
        let foundedID = findChoosenId(choosen);
        const pushedArr = elementToArr(choosensArr, foundedID);
        setChoosensCookie(pushedArr);
      }, true);
    });
  }

  if(favoritesFromCookies) {
    favoritesFromCookies.forEach((item) => {
      customForEach(choosens, function (index, choosen) {
        let foundedID = findChoosenId(choosen);
        if(item === foundedID) {
          const choosenBtn = choosen.querySelector('.ChoosenBtnJs');
          choosenBtn.classList.toggle('active-svg');
        }
      });
    });
  }

})();