(() => {

  const isFewElements = (elements, btn) => {
    const elementsArr = Array.prototype.slice.call(elements);
    if(elements.length < 7) {
      hideElement(btn);
    } else {
      elementsArr.forEach((element, index, elementsArr) => {
        if(index > 7) {
          element.classList.toggle('display-none');
        }
      });
    }
  }

  const hideElement = (element) => {
    element.style.display = 'none';
  }

  const isElementHidden = (element) => {
    return window.getComputedStyle(element, null).getPropertyValue('display') === 'none';
  }

  const showContent = (elements, howToDisplay, btn, callback) => {

    let element, index;

    elements = elements.length ? elements : [elements];

    for (index = 0; index < elements.length; index++) {
      element = elements[index];
      if (isElementHidden(element)) {
        element.style.display = '';
        if (isElementHidden(element)) {
          element.style.display = howToDisplay || 'block';
        }
      }
    }

  }

  const toggleContent = (elements, howToDisplay) => {

    let element, index;

    elements = elements.length ? elements : [elements];

    for (index = 0; index < elements.length; index++) {
      element = elements[index];
      if (isElementHidden(element)) {
        element.style.display = '';
        if (isElementHidden(element)) {
          element.style.display = howToDisplay || 'block';
        }
      } else {
        element.style.display = 'none';
      }
    }

  }

  const aboutBtn = document.querySelector('.about__btn');
  if(aboutBtn) {
    aboutBtn.addEventListener('click', () => {
      toggleContent(document.querySelector('.activity'));
    });
  }

  const otherBtn = document.querySelector('.display-other');
  if(otherBtn) {
    otherBtn.addEventListener('click', () => {
      toggleContent(document.querySelector('.other'), 'flex');
    });
  }

  const servicesBtn = document.querySelector('.services__btn');
  const servicesBlocks = document.querySelectorAll('.services__block');
  if(servicesBtn) {
    isFewElements(servicesBlocks, servicesBtn);
    servicesBtn.addEventListener('click', () => {
      showContent(servicesBlocks, 'flex', servicesBtn, hideElement(servicesBtn));
    });
  }

  const certificatesBtn = document.querySelector('.certificates__load-btn');
  if(certificatesBtn) {
    certificatesBtn.addEventListener('click', () => {
      showContent(document.querySelectorAll('.certificates__container .display-none'), 'block', certificatesBtn, hideElement(certificatesBtn));
    });
  }

})();